<template>
  <a class="prize" :href="myPrizeHref">
    <img :src="picSrc">
  </a>
</template>

<script>
import ASIGN from '@/config/const'
export default {
  name: 'commonPrize',
  data () {
    return {
      myPrizeHref: '/myprize.html',
      picSrc: ASIGN.cdnUrl + '/common/my_prize.png'
    }
  },
  props: {
    path: {
      type: String,
      default: 'common'
    }
  },
  watch: {
    path (newValue, oldValue) {
      this.myPrizeHref = '/myprize.html?activityKey=' + newValue
    }
  }
}
</script>

<style lang="less" scoped>
  .prize{
    color: #fff;
    width: 1.37rem;
    height: 1.37rem;
    img{
      width: 1.37rem;
      height: 1.37rem;
    }
  }
</style>



