<template>
  <a href="javascript:;" class="rule" @click="showRuleModal">
    <img :src="ruleSrc">
  </a>
</template>

<script>
import ASIGN from '@/config/const'
export default {
  name: 'CommonRule',
  data () {
    return {
      ruleSrc: ASIGN.cdnUrl + '/common/rule.png'
    }
  },
  methods: {
    showRuleModal () {
      this.$emit('showRuleModal')
    }
  }
}
</script>

<style lang="less" scoped>
  .rule{
    width: .98rem;
    height: .4rem;
    img{
      display: block;
      width: 100%;
    }
  }
</style>



