<template>
  <div class="my-prize-tip">
    <div class="tip-box" :style="style">
      <div class="btn-close" @click="bindClose">
        <img :src="closeSrc">
      </div>
      <div class="img">
        <img :src="cartoonSrc">
      </div>
      <div class="title">抽奖次数已用完</div>
      <div class="subtitle">今日抽奖次数已用完，明日继续参与活动抽奖吧！</div>
      <a href="/myprize.html" class="chakan">查看我的奖品</a>
    </div>
  </div>
</template>

<script>
import { getZIndex } from '../../utils/zindex'
import ASIGN from '@/config/const'
export default {
  name: 'commonNomoretip',
  data () {
    return {
      zIndex: getZIndex() + 3,
      closeSrc: ASIGN.cdnUrl + '/common/close@3x.png',
      cartoonSrc: ASIGN.cdnUrl + '/common/cartoon@3x.png'
    }
  },
  props: {
  },
  methods: {
    bindClose () {
      this.$emit('bindClose')
    }
  },
  computed: {
    style () {
      return {
        'z-index': this.zIndex
      }
    }
  }
}
</script>

<style lang="less" scoped>
.my-prize-tip {
  .tip-box {
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -3.15rem;
    margin-top: -3.36rem;
    width: 6.3rem;
    height: 6.72rem;
    background-color: #fff;
    border-radius: 0.16rem;
    .img {
      width: 2.41rem;
      height: 2.56rem;
      margin: 0.46rem auto 0.51rem;
      img{
        display: block;
        width: 100%;
      }
    }
    .btn-close {
      position: absolute;
      top: -0.74rem;
      right: 0;
      width: 0.5rem;
      height: 0.5rem;
      img{
        display: block;
        width: 100%;
      }
    }
    .title,.subtitle {
      text-align: center;
    }
    .title {
      font-size: 0.32rem;
      color: #222222;
      letter-spacing: 0;
    }
    .subtitle {
      margin-top: 0.12rem;
      font-size: 0.26rem;
      color: #666666;
      letter-spacing: 0;
    }
    .chakan {
      display: block;
      width: 3.15rem;
      height: 0.8rem;
      margin: 0.35rem auto 0;
      background: #fb5b2b;
      border: 2px solid #fb5b2b;
      border-radius: 1rem;
      font-size: 0.32rem;
      color: #ffffff;
      letter-spacing: 0;
      line-height: 0.8rem;
      text-align: center;
    }
  }
}
</style>



